import { defineProps, defineExpose, defineComponent, PropType, computed, toRefs, ExtractPropTypes } from 'vue';
import { ElTable, ElTableColumn } from 'element-plus';

interface APITableColumnInfo {
    [key: string]: any;
}

// todo 类型定义
export interface APITableColumn {
    prop: string;
    label: string;
    width?: string | number;
    minWidth?: string | number;
    render?: (row: APITableColumnInfo) => any;
    [key: string]: any;
}

export default defineComponent({
    props: {
        tableColumns: {
            type: Object as PropType<APITableColumn[]>,
            required: true,
        },
        tableData: {
            type: Array as PropType<APITableColumnInfo[]>,
            required: true,
        },
    },
    setup(props) {
        return () => (
            <ElTable data={props.tableData}>
                {
                    props.tableColumns.map((row) => {
                        return (
                            <ElTableColumn
                                {...row}
                                v-slots={{
                                    default: row.render,
                                }}>
                            </ElTableColumn>
                        );
                    })
                }
            </ElTable>
        );
    },
});